










import Vue from 'vue'
import { Component, Prop } from 'vue-property-decorator'
import axios from 'axios'
import Banner from './Banner.vue'

@Component({
  components: { Banner },
})
export default class BannerGallery extends Vue {
  banners = new Array<{ name: string, description: string, background: string, color: string }>()
  active = 0

  get banner() {
    return this.banners[this.active]
  }

  Switch(index: number) {
    this.active = index
  }

  mounted() {
    axios.get('./homepage.json').then(response => {
      this.banners = response.data.banners
    })
  }
}
